.page-body {
  background-color: #FFFFFF;
  margin: 2rem auto 1rem auto;
  width: 90%;
  padding: 1rem;
  max-width: 640px; }

@media only screen and (min-width:64.063em) {
  .page-body {
    max-width: 1600px; }
  .two-column-grid {
    float: left;
    width: 44%;
    margin: 0 3%; } }

.event-intro .event-intro-heading {
  font-size: 1.125rem; }
.event-intro .event-intro-text {
  font-size: 1.125rem; }

.event-calendar .event-header {
  font-size: 1.125rem; }
.event-calendar .event-header-text {
  vertical-align: middle;
  display: inline-block; }
.event-calendar .event-icon {
  width: 1.125rem; }
.event-calendar .event-details {
  margin-bottom: 10px; }
.event-calendar .button.register-now {
  margin: 0;
  display: block;
  padding: 15px;
  font-size: 1.5rem;
  letter-spacing: 1px;
  text-decoration: underline; }
.event-calendar .event-timetable {
  position: relative; }
  .event-calendar .event-timetable .vertical-line {
    position: absolute;
    width: 4px;
    height: 100%;
    left: 30px;
    top: 0;
    background-color: #0AAB8A;
    box-shadow: 0 0 4px #3DB0B4; }
  .event-calendar .event-timetable .timetable-item {
    padding-bottom: 30px;
    padding-left: 66px; }
    .event-calendar .event-timetable .timetable-item.timetable-day {
      font-size: 1.3125rem;
      padding-top: 6px;
      padding-bottom: 6px;
      margin-bottom: 15px;
      background-color: #E0E0E0; }
    .event-calendar .event-timetable .timetable-item .timetable-icon {
      background-color: #0AAB8A;
      background-repeat: no-repeat no-repeat;
      background-size: contain;
      background-origin: content-box;
      background-position: center center;
      border: 4px solid #F2F2F2;
      border-radius: 100%;
      box-shadow: 0 0 4px 1px rgba(0, 0, 0, 0.5);
      content: "";
      display: block;
      height: 42px;
      position: absolute;
      padding: 4px;
      left: 11px;
      width: 42px; }
      .event-calendar .event-timetable .timetable-item .timetable-icon.icon-conference {
        background-image: url(/img/icons/conference.png); }
      .event-calendar .event-timetable .timetable-item .timetable-icon.icon-lunch {
        background-image: url(/img/icons/lunch.png); }
      .event-calendar .event-timetable .timetable-item .timetable-icon.icon-coffee {
        background-image: url(/img/icons/coffee-cup.png); }
      .event-calendar .event-timetable .timetable-item .timetable-icon.icon-registration {
        background-image: url(/img/icons/security-camera.png); }
      .event-calendar .event-timetable .timetable-item .timetable-icon.icon-workshop {
        background-image: url(/img/icons/workshop-tool.png); }
      .event-calendar .event-timetable .timetable-item .timetable-icon.icon-quickies {
        background-image: url(/img/icons/comet.png); }
    .event-calendar .event-timetable .timetable-item .timetable-heading, .event-calendar .event-timetable .timetable-item .timetable-description {
      margin: 1rem 0; }
    .event-calendar .event-timetable .timetable-item .timetable-heading {
      font-weight: bold; }

.wall-container {
  box-shadow: 0 0 4px 1px rgba(0, 0, 0, 0.2); }
  .wall-container .wall-info-panel {
    position: relative; }
  .wall-container .wall-content {
    background-image: url(/img/wall/wall-2-opt.png);
    background-repeat: no-repeat;
    background-size: cover;
    color: #F2F2F2;
    padding: 1rem;
    font-size: 1.125rem; }
  .wall-container .button.register-now {
    font-size: 1rem;
    letter-spacing: 1px;
    margin: 0;
    padding: 10px;
    position: absolute;
    left: 0;
    top: 0;
    text-decoration: underline; }
  @media only screen and (min-width:30em) {
    .wall-container .button.register-now {
      top: auto;
      bottom: 1rem;
      left: 1rem; } }

.ideal-candidate-panel {
  color: #8C0000;
  margin: 1rem 0;
  box-shadow: 0 0 4px 1px rgba(0, 0, 0, 0.4);
  padding: 0.5rem 1rem;
  border: 1px solid; }
  .ideal-candidate-panel .ideal-candidate-text {
    font-size: 1.125rem; }
  .ideal-candidate-panel .pirate-cap {
    width: 35%;
    float: left;
    margin-right: 5%; }
  .ideal-candidate-panel h2 {
    color: #8C0000; }
  .ideal-candidate-panel ol {
    margin-bottom: 0; }
  @media only screen and (min-width:64.063em) {
    .ideal-candidate-panel .pirate-cap {
      width: 30%;
      margin-right: 5%; } }

.event-people h2 {
  margin-bottom: 0; }
.event-people .people-list {
  list-style: none;
  margin: 0; }
  .event-people .people-list a {
    text-decoration: underline; }
.event-people .people-list-item {
  box-shadow: 0 0 4px 1px rgba(0, 0, 0, 0.2);
  margin-bottom: 1rem; }
.event-people .people-image {
  max-width: 120px; }
.event-people .people-name, .event-people .people-short-bio, .event-people .inline-list {
  padding: 0 5px;
  margin: 0; }
.event-people .inline-list {
  margin-left: -1.375rem; }
@media only screen and (min-width:30em) {
  .event-people .people-image {
    float: left;
    max-width: 135px; }
  .event-people .people-content {
    margin-left: 145px; } }

.contact-us-panel {
  font-size: 1.5rem; }

